<template>
  <div class="mt-5">
    <v-btn @click="dialog = true"> 주소 찾기 </v-btn>
    <div>{{ address }}{{ buildingName }}</div>
    <div>{{ oldAddress }}</div>
    <div>{{ post }}</div>
    <v-dialog
      v-model="dialog"
      max-width = "600"
    >
      <v-card>
        <vue-daum-postcode @complete="ClickPost($event)"/>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  data: () => ({
    dialog: false,
    result: null,
    address: null,
    buildingName: null,
    oldAddress: null,
    post: null
  }),
  computed: {
  },
  watch:{
  },
  mounted(){
    this.$store.dispatch('route','/8_daumPostcode');
  },
  methods:{
    ClickPost(event){
      console.log(event);
      this.result = event;
      this.address = event.address;
      this.buildingName = event.buildingName;
      this.oldAddress = event.autoJibunAddress;
      this.post = event.zonecode;
      this.dialog = false;
    }
  }
}

</script>
<style>
  .vue-daum-postcode-container{
    height: 450px !important;
  }
</style>